import React from 'react';
import {
  NavBtn
} from './navbarstyle';
import { CgFileDocument, CgGitFork } from "react-icons/cg";
import { AiFillStar, AiOutlineHome, AiOutlineFundProjectionScreen, AiOutlineUser, AiFillGithub, AiFillLinkedin, AiOutlineMail } from "react-icons/ai";
import { NavLink as Link } from 'react-router-dom';
import pdf from './Assets/resum√©2.pdf';


const Navigate = () => {
  return (
    <>
      {/* justify content in nav is sperating credential, div & navbtn */}
      <nav className="navBar">
        <a className="Credentials" href='/portfolio/'>
            KA.
        </a>
        <div className="navmenu">
          <Link className="navlink1" to='/'>
            <AiOutlineHome style={{fontSize: "4.21vh", padding: "0.412vw"}}/>Home
          </Link>
          <Link className="navlink1" to='/about'>
            <AiOutlineUser style={{fontSize: "4.21vh", padding: "0.412vw"}} />About
          </Link>
          <Link className="navlink1" to='/projects'>
            <AiOutlineFundProjectionScreen style={{fontSize: "4.21vh", padding: "0.412vw"}} />Projects
          </Link>
          <a className="navlink1" href={pdf} target='_blank' rel='noreferrer'>
            <CgFileDocument style={{fontSize: "4.21vh", padding: "0.412vw"}} />Resume
          </a>
          <Link className="navlink1" to='/contact'>
            <AiOutlineMail style={{fontSize: "4.21vh", padding: "0.412vw"}} />Contact
          </Link>
        </div>
        <NavBtn>
          <a className="starButton" href="https://github.com/kush458/portfolio" target="_blank" rel="noreferrer"> 
          {/* _blank means open the doc in new tab */}
            <CgGitFork style={{fontSize: "2.74vh", alignSelf: "center", paddingBottom: "2px"}} />
            <AiFillStar style={{fontSize: "2.74vh", alignSelf: "center", paddingBottom: "2px"}} />
          </a>
          <a className="starButton" href="https://github.com/kush458" target="_blank" rel="noreferrer"> 
          {/* _blank means open the doc in new tab */}
            <AiFillGithub style={{fontSize: "2.74vh", alignSelf: "center", paddingBottom: "2px"}} />
          </a>
          <a className="starButton" href="https://www.linkedin.com/in/kushar77/" target="_blank" rel="noreferrer"> 
          {/* _blank means open the doc in new tab */}
            <AiFillLinkedin style={{fontSize: "2.74vh", alignSelf: "center", paddingBottom: "2px"}} />
          </a>
        </NavBtn>
      </nav>
    </>
  );
};

export default Navigate;