import React from 'react';
import TypeWritterEffect from 'react-typewriter-effect';

const Typing = () => {
    return (
        <TypeWritterEffect 
        textStyle={{
          color: '#fff',
          fontWeight: 'lighter',
          fontSize: '2.4rem',
          paddingLeft: '5.2rem',
          paddingTop: '1.8rem',
          letterSpacing: '2.5px',
        }}
        startDelay={250}
        cursorColor="#02aab0"
        multiText={[
          // 'Pursuing,',
          // 'Computer Engineering',
          // 'at,',
          // 'The University of British Columbia',
          'Perseverant, Performance-oriented, Passionate.',
          // 'Performance Oriented,',
          // 'Passionate.',
        ]}
        multiTextDelay={520}
        typeSpeed={108}
        multiTextLoop={false}
        />
    );
}

export default Typing;