import React, { useEffect, useRef } from 'react';
import {Row, Col, Container} from 'react-bootstrap';
import wikiProject from './Assets/wikipediacache.jpg';
import graphproject from './Assets/graphproject.png';
import scancomproject from './Assets/scancomproject.png';
import os from './Assets/osproject.jpg';
import risc from './Assets/riscproject.png';
import VanillaTilt from 'vanilla-tilt';
import WindowSize from '../utils/windowSize';

const Tilt = (props) => {
    const {options, ...rest} = props;
    const tilt = useRef(null);

    useEffect(() => {
        VanillaTilt.init(tilt.current, options);
    }, [options]);

    return <div ref={tilt} {...rest} />
}

const Card = (props) => {
    const { width, height } = WindowSize();

    const options = {
        scale: 1,
        speed: 7000,
        max: 18,
        reverse: false,
    } 
    if(props.number === "5") {
        return(
        <>
            <Tilt options={options} id={props.id} style={{marginLeft: "7vw"}}>
                {width < 1920 && <div className="row" style={{display: "inline-block"}}>
                    <div>
                        <img alt='' src={props.imgSrc} className="projectPic" />
                    </div>
                    <div className="col">
                        <h1 className="projectTopic" style={{marginTop: "2.23vh"}}>{props.topic}</h1>
                        <p className="projectPara" style={{marginTop: "1.8vh"}}>{props.text5}</p>
                        <p className="projectPara">{props.text5_2}</p>
                    </div>
                </div>}
                {width >= 1920 && <div className="row" style={{display: "inline-block"}}>
                    <div>
                        <img alt='' src={props.imgSrc} style={{ height: "24.1vh", width: "18vw", marginTop: "2.32vh", borderRadius: "2px", marginLeft: "0.52vw"}} className="projectPic" />
                    </div>
                    <div className="col">
                        <h1 className="projectTopic" style={{marginTop: "2.23vh", fontSize: "3.55vh"}}>{props.topic}</h1>
                        <p className="projectPara" style={{marginTop: "1.8vh", fontSize: "2.32vh"}}>{props.text5}</p>
                        <p className="projectPara" style={{fontSize: "2.32vh"}}>{props.text5_2}</p>
                    </div>
                </div>}
            </Tilt>
        </>
        )
    }
    return(
        <Tilt options={options} id={props.id} style={{marginLeft: "7vw"}}>
            {width < 1920 && <div className="row">
                <img alt='' src={props.imgSrc} style={{ height: "175px", width: "295px", marginTop: "2.32vh", borderRadius: "2px"}} />
                <div className="col" style={{marginLeft: "1vw", marginRight: "1vw"}}>
                    <h1 className="projectTopic" style={{marginTop: "2.32vh"}}>{props.topic}</h1>
                    <p className="projectPara" style={{marginTop: "1.8vh"}}>{props.text}</p>
                </div>
            </div>}
            {width >= 1920 && <div className="row">
                <img alt='' src={props.imgSrc} style={{ height: "24.1vh", width: "18vw", marginTop: "2.32vh", borderRadius: "2px", marginLeft: "0.52vw"}} />
                <div className="col" style={{marginLeft: "1vw", marginRight: "1vw"}}>
                    <h1 className="projectTopic" style={{marginTop: "2.32vh", fontSize: "2.32vh"}}>{props.topic}</h1>
                    <p className="projectPara" style={{marginTop: "1.8vh", fontSize: "2.32vh"}}>{props.text}</p>
                </div>
            </div>}
        </Tilt>
    )
}
const Cards = (props) => {
    const { width, height } = WindowSize();
    return(
        <section>  
            {width < 1920 && <Row>
                <Card id="card1" imgSrc={wikiProject} topic="Wikipedia Cache" text={props.text1} number="1" />
                <Card id="card2" imgSrc={graphproject} topic="Graph ADT development" text={props.text2} number="2" />
                <Card id="card3" imgSrc={scancomproject} topic="Scancom App" text={props.text3} number="3" />
                <Card id="card4" imgSrc={os} topic="Operating Systems Project" text={props.text4} number="4"/>
                <Card id="card5" imgSrc={risc} topic="RISC Machine" text5={props.text5} text5_2={props.text5_2} number="5" />                              
            </Row>}
            {width >= 1920 && <Row>
                <Card id="card1ForBigScreen" imgSrc={wikiProject} topic="Wikipedia Cache" text={props.text1} number="1" />
                <Card id="card2ForBigScreen" imgSrc={graphproject} topic="Graph ADT development" text={props.text2} number="2" />
                <Card id="card3ForBigScreen" imgSrc={scancomproject} topic="Scancom App" text={props.text3} number="3" />
                <Card id="card4ForBigScreen" imgSrc={os} topic="Operating Systems Project" text={props.text4} number="4"/>
                <Card id="card5ForBigScreen" imgSrc={risc} topic="RISC Machine" text5={props.text5} text5_2={props.text5_2} number="5" />                              
            </Row>}
            {/* <Row>
                <Card id="card4" imgSrc={os} topic="Operating Systems Project" text={props.text4} number="4"/> 
                <Card id="card5" imgSrc={risc} topic="RISC Machine" text5={props.text5} text5_2={props.text5_2} number="5" />                             
            </Row> */}
        </section>
    )
}
export default Cards;

