import { HiOutlineChevronDoubleLeft, HiOutlineChevronDoubleRight } from 'react-icons/hi';
import pdf from './Assets/resum√©2.pdf';

const Tilt = (props) => {
    const {options, ...rest} = props;
    const tilt = useRef(null); /*Essentially, useRef is like a ‚Äúbox‚Äù that can hold a mutable value in its .current property */

    useEffect(() => {
        VanillaTilt.init(tilt.current, options);
    }, [options]);

    return <div ref={tilt} {...rest}></div>
}


const Smallercard = (props) => {
    const options = {
        scale: 1.3,
        speed: 2500,
        max: 25,
        reverse: false,
    }
    return(
        <Tilt options={options} className={props.cardClass}>
            <p className={props.textClass}>{props.text}</p>
        </Tilt>
    );
}

