import React, { useEffect, useState } from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import programmer from './Assets/illustration_green.png';
import Typing from './typing';
import arrow from './Assets/arrow.png';
import { Link, withRouter } from 'react-router-dom';
import WindowSize from '../utils/windowSize';

const Home = () => {
    // xs={the number of columns you wish to span for Extra small devices Phones (<768px)}
    // md={the number of columns you wish to span for Medium devices Desktops (‚â•992px)}
    const [startDescribe, setDescribe] = useState(false);
    const { width, height } = WindowSize(); 
    return(
        <section>
            <Container className="home">
                <Row>
                    {/*One col for name*/}
                    <Col md={7} className="homeColumn1">
                        <h1 className="greeting">Hello<span className="wavingHand"> üëã</span>, there!</h1>
                        <h1 className="intro">I am <span className="fullName">Kush Arora.</span></h1>
                        {startDescribe ? <Typing /> : null}
                        <Link className="homeBtn" to={startDescribe ? "/about" : "#"} onClick={()=>{
                            setDescribe(true);
                        }}>{startDescribe ? "Neat! Tell me more" : "The 3 'P's that describe me"}</Link>
                        {/* <hr className="line"/>
                        <Typing /> */}
                    </Col>
                    
                    {/*Another for illustration*/}
                    <Col md={5}>
                        <img alt="programmer" src={programmer} className="imgHome" />
                    </Col>
                </Row>
                {width > 1920 && width < 2300 && <div>
                    <img alt="arrow" src={arrow} style={{height: "108px", width: "81px", position: "absolute", zIndex: "25", left: "52vw", bottom: "70vh"}} />
                    <p className="newFont" style={{bottom: "79vh"}}>A computer engineering student</p>
                </div>}
                {width === 1920 && <div>
                    <img alt="arrow" src={arrow} style={{height: "108px", width: "81px", position: "absolute", zIndex: "25", left: "53.5vw", bottom: "70vh"}} />
                    <p className="newFont" style={{bottom: "79vh"}}>A computer engineering student</p>
                </div>}
                {width > 1536 && width < 1920 && <div>
                    <img alt="arrow" src={arrow} style={{height: "108px", width: "81px", position: "absolute", zIndex: "25", left: "54.4vw", bottom: "67vh"}} />
                    <p className="newFont" style={{bottom: "79vh"}}>A computer engineering student</p>
                </div>}
                {width === 1536 && <div>
                    <img alt="arrow" src={arrow} style={{height: "108px", width: "5.2vw", position: "absolute", zIndex: "25", left: "54.4vw", bottom: "62.8vh"}} />
                    <p className="newFont" style={{bottom: "75.1vh"}}>A computer engineering student</p>
                </div>}
                {width < 1536 && width >= 1396 && <div>
                    <img alt="arrow" src={arrow} style={{height: "108px", width: "81px", position: "absolute", zIndex: "25", left: "53.5vw", bottom: "58vh"}} />
                    <p className="newFont">A computer engineering student</p>
                </div>}
                {width < 1396 && width >= 1255 && <div>
                    <img alt="arrow" src={arrow} style={{height: "108px", width: "81px", position: "absolute", zIndex: "25", left: "54.4vw", bottom: "59vh"}} />
                    <p className="newFont" style={{bottom: "72.7vh"}}>A computer engineering student</p>
                </div>}
                {width < 1255 &&  width > 1024 && <div>
                    <img alt="arrow" src={arrow} style={{height: "108px", width: "81px", position: "absolute", zIndex: "25", left: "54.4vw", bottom: "55.3vh"}} />
                    <p className="newFont" style={{bottom: "71vh"}}>A computer engineering student</p>
                </div>}
            </Container>
        </section>
    )
}
export default withRouter(Home);