import React, { useRef, useState } from 'react';
import { Row, Col, Container } from 'react-bootstrap';
import { IoIosSend } from 'react-icons/io';
import { GoCheck } from 'react-icons/go';
// import Navigate from './navigate';
import { SiInstagram, SiMessenger, SiWhatsapp } from 'react-icons/si';
import emailjs from 'emailjs-com';


const Loader = () => {
    return(
        <div className="loader">
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
        </div>
    )
}
const Contact = () => {
    const [sent, setSent] = useState(false);
    const [isLoading, setLoading] = useState(false);

    const form = useRef();
    const handleSubmit = (e) => {
        e.preventDefault();
        setLoading(true);
        emailjs.sendForm('service_3pgk7me', 'template_ecxqmg9', form.current, 'user_wR79lRfT5kOYAPHeINB12')
        .then((result) => {
            setSent(true);
        },
        (error) => {
            alert("An error occured, kindly try again", error.text);
        });
    };

    return(
        <Container>
            <Row className="justify-content-md-center">
                <Col md={5}>
                    <h1 className="contactHeading">Let's get in <span className="contactHeadingSpan">touch!</span></h1>
                    <p className="contactText">Feel free to send me a message on the right and I will respond as soon as possible.</p>
                    <p className="contactText">If you prefer to contact me directly, send an e-mail to: <a className="contactTextSpan" href="mailto: kusharora339@gmail.com">kusharora339@gmail.com</a></p>
                    {/* <p className="contactText">You can also <span className="contactTextSpan">DM</span> me through: 
                        <a className="dmLink" href="https://m.me/KushArora" target="_blank" rel="noreferrer">
                            {" "}<SiMessenger className="dmLink" />
                        </a> , 
                        <a className="dmLink" href="https://www.instagram.com/kush.arora.777/" target="_blank" rel="noreferrer">
                            {" "}<SiInstagram className="dmLink" />
                        </a> or
                        <a className="dmLink" href="https://api.whatsapp.com/send?phone=+918527478225&text=Hello%20Kush!" target="_blank" rel="noreferrer">
                            {" "}<SiWhatsapp className="dmLink" />
                        </a>
                    </p> */}
                </Col>
                <Col md={7}>
                    <form ref={form} autoComplete="off" className="contactForm" onSubmit={handleSubmit}>
                        <div>
                            <input autoComplete="new-name" type="text" placeholder="Name" className="textInput" name="new-name" required />
                        </div>
                        <div style={{marginTop: "2.5rem"}}>
                            <input autoComplete="new-email" type="email" placeholder="E-mail" className="textInput" name="new-email" required />
                        </div>       
                        <div style={{marginTop: "2.5rem"}}>
                            <textarea name="message" placeholder="Your message" className="textmsgInput" required />
                        </div> 
                        <div className="sendbtnFlex"> {
                            <button type="submit" className="sendButton">
                                {/* {sent ? <GoCheck style={{fontSize: "1.3em"}} /> : <IoIosSend style={{fontSize: "1.3em"}} />} */}
                                {
                                    isLoading ? 
                                    (sent ? <GoCheck style={{fontSize: "1.3em"}} /> : <Loader />) : 
                                    <IoIosSend style={{fontSize: "1.3em"}} />
                                }
                                {/* <Loader /> */}
                            </button>} 
                        </div>          
                    </form>
                </Col>
            </Row>
        </Container>
    )
}
export default Contact;